:root {
  --primary-clr-bg: hsl(0, 100%, 74%);
  --primary-clr-btn: hsl(154, 59%, 51%);
  --accent-clr: hsl(248, 32%, 49%);
  --neutral-clr-drkblue: hsl(249, 10%, 26%);
  --neutral-clr-grayblue: hsl(249, 10%, 26%);
  --neutral-clr-white: hsl(0, 0%, 100%) ;
  --ff:"Poppins", sans-serif;
  --fs-xlrg:2.25rem;
  --fs-lrg:1.625rem;
  --fs-body:1rem;
  --fs-med:.875rem;
  --fs-sml:.75rem;
  --fs-xsml:.625rem;
  --fw-bold: 700;
  --fw-semi-bold: 600;
  --fw-med: 500;
  --fw-reg: 400;
}

*, *::before, *::after {
  box-sizing: border-box;
  font-size: 100%;
  margin: 0;
  padding: 0;
}

html:focus-within {
  scroll-behavior: smooth;
}

a, a:visited, a:hover {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: none;
  color: inherit;
  cursor: pointer;
}

body {
  font-family: var(--ff);
  font-size: var(--fs-body);
  line-height: 1.5;
  color: var(--neutral-clr-white);
  font-weight: var(--fw-med);
  background-color: var(--primary-clr-bg);
  background-image: url(/images/bg-intro-mobile.png);
}
@media (min-width: 71.875em) {
  body {
    height: 100vh;
  }
}

.container {
  max-width: 69.375rem;
  margin: 0 auto;
  padding: 1.5625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.button__primary {
  background-color: var(--primary-clr-btn);
}

.button__secondary {
  background-color: var(--accent-clr);
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.hero {
  text-align: center;
  letter-spacing: 0.0625rem;
  line-height: 1.7;
  padding-top: 6.25rem;
}
.hero__title {
  font-size: var(--fs-lrg);
  font-weight: var(--fw-bold);
  line-height: 1.3;
  margin-bottom: 1.875rem;
}
@media (min-width: 40em) {
  .hero__title {
    font-size: var(--fs-xlrg);
  }
}
.hero__text {
  font-size: var(--fs-med);
  margin-bottom: 3.75rem;
}